import { useContext, useEffect, useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import { UserProfileID } from "../contexts/UserProfileID";

const Create = () => {
    const {profile_id} = useContext(UserProfileID);
    const [title,setTitle] =useState('');
    const [body,setBody] = useState('');
    const [author,setAuthor] =useState('');
    const [isPending, setIsPending] = useState(false);
    const history = useHistory();
    const inputRef = useRef();

    useEffect(() => {
        inputRef.current.focus();
    }, []);

    const handleSubmit= (e) => {
        e.preventDefault();
        const post = {title , body, author,author_id: profile_id};
        setIsPending(true);
        let requestOptions = {
            method: 'POST',
            headers: {"Content-Type" : "application/json"},
            body : JSON.stringify(post)
        };
        let url = "https://backend-react-json-server.herokuapp.com/posts"
          fetch(url, requestOptions)
          .then((res) => {
              if(res.status!==201){
                throw Error(res.statusText);
              }else{
                console.log("Posted");
                setIsPending(false);
                history.push('/react-blog-test-v2');
              }
          })
          .catch((err) => {
              console.log(err.message);
              setIsPending(false);
          });

    }
    return (
        <div className="create">
            <h2>New Post</h2>
            <form onSubmit={handleSubmit}>
                <label>Post Title : </label>
                <input 
                    type="text"
                    required
                    value={title}
                    onChange={(e)=>setTitle(e.target.value)}
                    ref={inputRef}
                />
                <label>Post Body : </label>
                <textarea
                    value={body}
                    onChange={(e)=>setBody(e.target.value)}
                    required
                />
                <label>Author Name : </label>
                <input 
                    value={author}
                    onChange={(e)=>setAuthor(e.target.value)}
                    type="text"
                    required
                />
               {!isPending&&<button type='submit'>
                    Post
                </button>}
                {isPending&&<button disabled>
                    Please Wait...    
                </button>}
            </form>
        </div>
    );
}
export default Create;
